export default {
	data() {
		return {

		}
	},
	onLoad() {

	},
	methods: {
		getUserInfo(res) {
			console.log(res);

			// 获取用户加密信息
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log('loginRes');
					console.log(loginRes);
					// 获取用户未加密信息
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							console.log('infoRes');
							console.log(infoRes);
							if (infoRes.iv) {
								console.log("登录成功");
								uni.setStorageSync('suid','suid');
								uni.setStorageSync('srand','srand');
								uni.navigateBack({
									url: "../index/index"
								})
							};
						}
					});

				}
			});


		}
	},
}
